<template>
  <div class="container">
    <b-modal ok-only id="msgmodal" title="Error">An error occured</b-modal>

    <form @submit.prevent="loadmembers()">
      <div class="form-group">
        <label for="inputKey">Group key</label>
        <input
          v-model="groupkey"
          type="text"
          class="form-control"
          id="inputKey"
          placeholder="Enter key of existing group"
        >
      </div>

      <b-button
        variant="primary"
        v-if="groupkey != ''"
        type="submit"
        class="btn btn-primary"
      >Load members</b-button>
    </form>
  </div>
</template>

<script>
export default {
  methods: {
    loadmembers: function() {
      this.$http
        .get("groups/" + this.groupkey + "/members")
        .then(response => {
          response.data.forEach(element => {
            element.isActive = false;
            element.amountIOTA = 0;
            element.message = "";
          });
          this.$router.push({
            name: "iotagroupmembers",
            params: { memberdata: response.data, groupkey: this.groupkey }
          });
        })
        .catch(error => {
          this.$bvModal.show("msgmodal");
        });
    }
  },
  data: function() {
    return {
      groupkey: ""
    };
  }
};
</script>
