<template>
  <div class="container">
    <b-modal id="seedmodal" title="Enter seed" @ok="payout()">
      <input v-model="seedIOTA" type="text" class="form-control" placeholder="Enter your seed">
    </b-modal>
    <b-modal
      ok-only
      @ok="payingout = false"
      @close="payingout = false"
      id="payoutmodal"
      :title="payoutheader"
    >{{payouttext}}</b-modal>

    <div class="text-center mt-5" v-if="payingout">
      <div class="spinner-border text-primary" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
    <div v-else>
      <h1>Existing members of group '{{groupkey}}'</h1>
      <ul class="list-group">
        <li class="list-group-item pb-0" v-for="member in members" :key="member.id">
          <header class="pb-3" @click="member.isActive = !member.isActive">
            <div>{{member.name}}</div>
            <a
              class="small"
              :href="'https://thetangle.org/address/' + member.firstAddress"
              target="_blank"
            >{{member.firstAddress}}</a>
          </header>
          <div class="p-3" v-if="member.isActive">
            <div class="form-group">
              <label :for="'inputAmountIOTA'+member.id">Amount of IOTA</label>
              <input
                v-model="member.amountIOTA"
                min="0"
                step="1"
                type="number"
                class="form-control"
                :id="'inputAmountIOTA'+member.id"
                placeholder="Enter a value"
              >
            </div>
            <div class="form-group">
              <label :for="'inputMessage'+member.id">Message</label>
              <input
                v-model="member.message"
                type="text"
                class="form-control"
                :id="'inputMessage'+member.id"
                placeholder="Enter a message"
              >
            </div>
            <button
              @click="preparePayout(member)"
              class="btn btn-primary"
            >Pay out to {{member.name}}</button>
          </div>
        </li>
      </ul>
    </div>
    <b-button class="mt-5" @click="$router.go(-1)">Go back</b-button>
  </div>
</template>

<script>
import IOTA from "iota.lib.js";
export default {
  props: ["memberdata", "groupkey"],
  methods: {
    preparePayout: function(member) {
      this.currentTransfer = [
        {
          value: member.amountIOTA,
          address: member.firstAddress,
          message: this.iota.utils.toTrytes(member.message)
        }
      ];
      this.$bvModal.show("seedmodal");
    },
    payout: function() {
      this.payingout = true;
      setTimeout(
        () =>
          this.iota.api.sendTransfer(
            this.seedIOTA,
            3,
            14,
            this.currentTransfer,
            (error, success) => {
              if (error) {
                console.log(error);
                this.payoutheader = "Error";
                this.payouttext = error;
              } else {
                console.log(success);
                this.payoutheader = "Success";
                this.payouttext = "Payment finished";
              }
              this.members.map(m => m.isActive = false);
              this.$bvModal.show("payoutmodal");
            }
          ),
        10
      );
    }
  },
  data: function() {
    return {
      members: this.memberdata, // direktes Verwenden geht zwar, ist aber dann nicht reaktive
      iota: new IOTA({
        provider: "https://nodes.thetangle.org:443"
      }),
      seedIOTA: "",
      currentTransfer: [],
      payoutheader: "",
      payouttext: "",
      payingout: false
    };
  }
};
</script>
